/**
 * js-sdk
 * https://mp.weixin.qq.com/wiki?t=resource/res_main&id=mp1421141115
 */
import wx from "weixin-js-sdk";
const wxApi = {
  /**
   * [wxRegister Api]
   * @param  {Function} callback [ready]
   */
  wxRegister(data, option) {
    //dataoption
    /*
    wx.config({
      debug: false, // 
      appId: data.appId, // 
      timestamp: data.timestamp, // 
      nonceStr: data.nonceStr, // 
      signature: data.signature, // 1
      jsApiList: [
        "checkJsApi",
        "updateTimelineShareData",
        "updateAppMessageShareData",
        "onMenuShareQQ",
        "onMenuShareWeibo"
      ] // JSJS2
    });
    */
   console.log(data,option)
    wx.config(data)
    wx.ready(function() {
      //  API
      var shareData = {
        title: option.title,
        desc: option.desc.replace(/&nbsp:/gi,""),
        link: option.link,
        imgUrl:
          "http://vod.vguan.net/link/j52020124714574755/1c3ab863ceb2c096094020527eeca21b.jpeg"
      };
      wx.onMenuShareAppMessage(shareData);
      wx.onMenuShareTimeline(shareData);
      wx.onMenuShareQQ(shareData);
      wx.onMenuShareWeibo(shareData);
    //   
      wx.updateTimelineShareData(shareData);
      wx.updateAppMessageShareData(shareData)

      //checkJsApi
      wx.checkJsApi({
        jsApiList: ["getLocation", "checkJsApi"],
        success: function(res) {
          if (res.checkResult.getLocation == false) {
            alert(
              "JS"
            );
            return;
          }
        }
      });
    });
  }
};
export default wxApi;



