// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import App from "./App";
import router from "./router";
import {
  Button,
  Carousel,
  Select,
  CarouselItem,
  Option,
  Tabs,
  TabPane,
  Icon,
  Backtop,
  Card,
  Loading,
} from "element-ui";
Vue.config.productionTip = false;
Vue.use(Button);
Vue.use(Carousel);
Vue.use(Select);
Vue.use(CarouselItem);
Vue.use(Option);
Vue.use(Tabs);
Vue.use(TabPane);
Vue.use(Icon);
Vue.use(Backtop);
Vue.use(Card);
Vue.use(Loading.directive);
/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  components: { App },
  template: "<App/>"
});



